.device-wrapper {
  max-width: 300px;
  width: 100%; }

.device {
  position: relative;
  background-size: cover; }
  .device::after {
    position: absolute;
    background-size: cover;
    width: 100%;
    height: 100%;
    pointer-events: none; }
  .device .screen {
    position: absolute;
    background-size: cover;
    pointer-events: auto; }
  .device .button {
    position: absolute;
    cursor: pointer; }

